<template>
  <div>
    
    <CRow class="mb-3">
      <CCol>
        <h1>Detail Stimulasi</h1>
      </CCol>
      
    </CRow>
    
    <CRow>
      <CCol col="12">
        <CCard>
          <CCardHeader>
            {{stimulation.stimulasi_judul}}
          </CCardHeader>
          <CCardBody>
            <div class="row">
              <div class="col-12 mb-3">
                <p>{{stimulation.detail_action}}</p>
              </div>
              <div class="col-12">
                <a class="d-block mb-3" :href="stimulation.video_embed" target="_blank">{{stimulation.video_embed}}</a>
                
              </div>
              
            </div>
          </CCardBody>
          <CCardFooter>
            <CRow>
              
              <CCol>
                <CRow class="justify-content-end">
                  <CCol col="6" sm="4" md="2" xl="2" class="mb-3 mb-xl-0">
                    <CButton block variant="outline" color="primary" @click="goBack()"> Back</CButton>
                  </CCol>
                </CRow>
              </CCol>
            </CRow>
            
          </CCardFooter>
        </CCard>
      </CCol>
    </CRow>
    
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'EditStimulation',
  data(){
    return {
      stimulation: []
    }
  },
  methods: {
    setDetailStimulation(data){
      this.stimulation = data;
    }
    
  },

  mounted() {
    let body = {
      admin_user_id : localStorage.getItem('admin_id')
    };

    axios
      .post('http://103.151.215.74/api/admin/stimulasi/detail/edit/' + localStorage.getItem('stimulasiId') , body, {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      })
      .then(
        // (response) => this.setThread(response.data.json_data)
        (response) => {
          // console.log("Berhasil : ", response);
          this.setDetailStimulation(response.data.json_data);
        }
        
      )
      // .catch(function (error) {
      //   // handle error
      //   // console.log("Gagal : ", error);
      // })
  },
  
}
</script>